.navbar{
    grid-area: menu;
    height: 100%;
    background-color: #0f0f0f;
    @include d-flex(flex-start,center);
    flex-direction: column;
    &--logo{
        width: 90px;
        height: auto;
        margin:0.5rem;
    }
    &--button{
        width: 180px;
        padding: 0.5rem;
        border-radius: 10px;
        border:0px;
        background-color: transparent;
        color:#f1f1f1;
        padding: 0 12px;
        height: 2.5rem;
        &:hover{
            background-color: #5346464f;
        }
        &:active{
            background-color: whitesmoke;
            /* background: #534646; */
            opacity: 0.3;
            color:white;
        }
        &>p{
            font-family: $project-font;
            font-weight: 700;
        }
    }
    
}

.active{
    background-color: $color-dark-grey;
    border-radius: 15px;
}